
import { defineComponent, reactive, toRefs ,onBeforeMount, inject } from 'vue'
interface sourceMod {
    otherCase: Array<any>
}
export default defineComponent ({
    props: {
        source: {
            type: Object,
            default: {
                otherCase: []
            }
        }
    },
	setup(props) {
        const $api :any = inject('$api')
		const dataMap = reactive({
			dialogFormVisible: Boolean<false>(),
            otherCaseVal: '',
            curIndex: 0 
		})
        const addInvolved = (idx:number) => {
            dataMap.dialogFormVisible = true
            dataMap.curIndex = idx
        }
        const deleteInvolved = (idx: number) => {
            props.source.otherCase.splice(idx, 1);
        }
        const save = () => {
            props.source.otherCase.splice(dataMap.curIndex+1, 0, { name: dataMap.otherCaseVal, value: '' })
            dataMap.dialogFormVisible = false
        }
        onBeforeMount(() => {
		})
		return {
			...toRefs(dataMap),
            addInvolved,
            deleteInvolved,
            save
		}
	}
})
