<template>
<div class="pub-form-item">
   
    <div v-for="(i, idx) in source.otherCase" class="pub-form-col-2" :key="idx">
        <span class="label">{{i.name}}</span>
        <div class="content content-box">
            <div class="left-content">
                <el-input v-model="i.value" plaiceholder="其他案件"></el-input>
            </div>
            <div class="right-content right-content-icon">
                <span v-if="idx > 0" class="pub-form-icon el-icon-remove remove-icon-color" @click="deleteInvolved(idx)"></span>
                <span class="pub-form-icon el-icon-circle-plus add-icon-color" @click="addInvolved(idx)"></span>
            </div>
        </div>
    </div>
    <el-dialog 
        title="新增案件" 
        v-model:propName="dialogFormVisible" 
        width="40%"
        >
        <div class="pub-form-mod pub-form-civil">
            <div class="pub-form-bd">
                <div class="pub-form-item pub-form-border">
                    <div class="pub-form-col-2 w100">
                        <span class="label">案件名称</span>
                        <div class="content">
                            <el-input v-model="otherCaseVal" minlength="1" maxlength="8" placeholder="案件名称"></el-input>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="save">确 定</el-button>
        </div>
    </el-dialog>
</div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs ,onBeforeMount, inject } from 'vue'
interface sourceMod {
    otherCase: Array<any>
}
export default defineComponent ({
    props: {
        source: {
            type: Object,
            default: {
                otherCase: []
            }
        }
    },
	setup(props) {
        const $api :any = inject('$api')
		const dataMap = reactive({
			dialogFormVisible: Boolean<false>(),
            otherCaseVal: '',
            curIndex: 0 
		})
        const addInvolved = (idx:number) => {
            dataMap.dialogFormVisible = true
            dataMap.curIndex = idx
        }
        const deleteInvolved = (idx: number) => {
            props.source.otherCase.splice(idx, 1);
        }
        const save = () => {
            props.source.otherCase.splice(dataMap.curIndex+1, 0, { name: dataMap.otherCaseVal, value: '' })
            dataMap.dialogFormVisible = false
        }
        onBeforeMount(() => {
		})
		return {
			...toRefs(dataMap),
            addInvolved,
            deleteInvolved,
            save
		}
	}
})
</script>